* {
    scroll-behavior: smooth;
    outline: none;
    cursor: none;
    &:focus,input::-moz-focus-inner, button::-moz-focus-inner{
        outline: 0;
    }
}


/*-  -*/
.pagination {
    @include regular;
    margin: 2rem 0;
}

/*- Placeholder -*/
.form-control {
    &::-webkit-input-placeholder {
        color: $gray-400;
    }

    &input::-moz-placeholder {
        color: $gray-400;
    }

    &:-ms-input-placeholder {
        color: $gray-400;
    }
}

/*-  -*/
.img-by {
    position: relative;
    width: 100%;
    height: 0;
    overflow: hidden;

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 102%;
        height: auto;
    }

    &-1by1 {
        padding-bottom: 100%;
    }

    &-4by3 {
        padding-bottom: 75%;
    }

    &-16by9 {
        padding-bottom: 56.25%;
    }

    &-5by2 {
        padding-bottom: 40%;
    }
}

.gotop {
    position: fixed;
    bottom: 16px;
    right: 1.2rem;
    z-index: 10;
    transform:rotate(-90deg);


    img {
        width: 30px;
        height: 30px;
        transform:scale(1,1);
        transition: all .3s ease-out;
        &:hover {
            transform:scale(1.2,1.2);
        }
    }
}

/*- Header -*/
.header {
    background: transparent;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    @include media-breakpoint-up(lg) {
        // position: fixed;
        // top: 16px;
        // left: 16px;
        &.sticky {
            position: fixed;
            background:  rgba(265,265,265,0.9);
            width: 100%;
            box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.3);
        }
    }
    @include media-breakpoint-down(md) {
        &.sticky {
            position: sticky;
            top: 0;
            left: 0;
            z-index: 1000;
            background-color: rgba(265,265,265,0.9);
            box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.3);
        }
    }
}

/*- Footer -*/
.footer {
    position: relative;
    overflow: hidden;
    box-shadow: 0 -5px 25px 5px rgba(15, 62, 149, 0.3);
    .footer-wrap {
        background: linear-gradient(90deg, rgba(15,62,149,1) 14%, rgba(126,203,237,1) 99%);
        background-size: 100%;
        padding: 30px 0;
        display: flex;
        justify-content: center;
        overflow: hidden;
        position: relative;
        .footer-top {
            text-align: center;
            margin-bottom: 1rem;
            img {
                width: 60px;
                height: auto;
            }
        }
        .footer-bottom {
            .footer-address {
                text-align: center;
                p {
                    color: white;
                    margin-bottom: 5px;
                }
            }
            .footer-social{
                @include make-row();
                display: flex;
                justify-content: center;
                .social-icon {
                    @include make-col(2);
                    @include make-col-ready();
                    padding: 0 4px;
                    &:hover {
                        img {
                            transform: scale(0.8);
                            transition: ease-in-out .3s;
                        }
                    }
                }

            }
        }
        &::before {
            content:'';
            background: url('../images/footerbg-left.png')no-repeat center left;
            background-size: 60%;
            width: 80%;
            height: 110%;
            position: absolute;
            left: 3%;
            bottom: -10%;
        }
        @media screen and (min-width: 1501px) {
            &::before {
                background-size: 60%;
                width: 60%;
                height: 130%;
                position: absolute;
                left: 3%;
                bottom: -30%;
            }
        }
        
    }
    .footer-copyright {
        background: #09265D;
        .copyright {
            p {
                margin: 0;
                padding: .5rem 0;
                color: white;
                font-size: .8rem;
                font-weight: 300;
                opacity: .5;
                text-align: center;
            }
        }
    }
}

/*- Font -*/
body {
    font-family: 'Prompt', sans-serif;
}