/*- Banner -*/
.idxBanner {
    height: 720px;
    position: relative;
    .banner-wrap {
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
        overflow: hidden;
        background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.2) 0%, rgba(0,0,0,0) 50%), /* niebieski */
                    radial-gradient(ellipse at center, rgba(255, 151, 82, 0.3) 0%, rgba(0,0,0,0) 60%), /*czerwony */
                    radial-gradient(ellipse at center, rgba(141, 233, 218, 0.3) 0%, rgba(0,0,0,0) 60%); /*zielony */
        background-repeat: 
            no-repeat, 
            no-repeat, 
            no-repeat;
        background-size: 
            900px 900px,
            900px 900px,
            900px 900px;
        background-attachment: fixed;
        animation:bganimation 6s linear infinite;
        @keyframes bganimation {
            0%  {
                background-position: 
                    -100% -100%,
                    0% 200%,
                    200% 200%,
                    200% -100%;
            }
           50% {
                background-position: 
                    50% 100%,
                    100% 0%,
                    -200% 100%,
                    0% 100%;
           }
           100% {
                background-position: 
                    -100% -100%,
                    0% 200%,
                    200% 200%,
                    200% -100%;
           }
        }
        &::before {
            content: '';
            background: url('../images/idxBanner-bg.jpg')no-repeat bottom;
            background-size: cover;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            animation-name: Scale; 
            animation-duration: 60s; 
            animation-timing-function: linear;
            animation-iteration-count: infinite; 

            @keyframes Scale {
                0% { 
                    transform: scale(1);
                }
                50% { 
                    transform: scale(1.5);
                }
                100% { 
                    transform: scale(1);
                }
            }
        }
        
    }
    .banner-title {
        padding-left: 3%;
        display: inline-block;
        position: relative;
        width: 100%;
        .banner-text{
            font-weight: 500;
            font-style: italic;
            font-size: 3.5rem;
            letter-spacing: 2px;
            line-height: 3.5rem;
            color: white;
            position: relative;
            width: 100%;
            text-shadow: 0.05em 0 0 rgba(38, 211, 75, 0.952),
            -2px -0.05em 0 rgba(36, 76, 255, 0.75),
            2px 0.05em 0 rgba(216, 15, 15, 0.75);
            animation: glitch 500ms infinite ;
            animation-name: Swing;    /* keyframe */
            animation-duration: 2s;    /**/
            animation-timing-function: linear;
            animation-iteration-count: infinite; 

            @keyframes Swing {
                0% { 
                    transform: translate(0,0);
                }
                20% { 
                    transform: translate(-4px,2px);
                }
                50% { 
                    transform: translate(-2px,-3px);
                }
                70% { 
                    transform: translate(-4px,2px);
                }
                100% { 
                    transform: translate(0,0);
                }
            }
            p {
                position: absolute;
                top: 0;
                left: 0;
                &:first-child {
                    animation: glitch 850ms infinite;
                    clip-path: polygon(0 20%, 100% 15%, 100% 75%, 0 90%);
                    transform: translate(-2px, -0.0125em);
                    /* color: green; */
                    opacity: 0.8;
                }
                &:last-child {
                    animation: glitch 550ms infinite;
                    clip-path: polygon(0 2%, 100% 0%, 100% 90%, 0 100%);
                    transform: translate(0.0125em, 2px);
                    /* color: red; */
                    opacity: 0.8;
                }
            }
            @keyframes glitch {
                0% {
                  text-shadow: 2px 0 0 rgba(38, 211, 75, 0.952),
                    -2px -2px 0 rgba(36, 76, 255, 0.75),
                    -2px 2px 0 rgba(216, 15, 15, 0.75);
                }
                14% {
                  text-shadow: 2px 0 0 rgba(38, 211, 75, 0.952),
                    -2px -2px 0 rgba(36, 76, 255, 0.75),
                    -2px 2px 0 rgba(216, 15, 15, 0.75);
                }
                15% {
                  text-shadow: -2px -2px 0 rgba(38, 211, 75, 0.952),
                  2px 2px 0 rgba(36, 76, 255, 0.75),
                    -2px -2px 0 rgba(216, 15, 15, 0.75);
                }
                49% {
                  text-shadow: -2px -2px 0 rgba(38, 211, 75, 0.952),
                    2px 2px 0 rgba(36, 76, 255, 0.75),
                    -2px -2px 0 rgba(216, 15, 15, 0.75);
                }
                50% {
                  text-shadow: 2px 2px 0 rgba(38, 211, 75, 0.952),
                    2px 0 0 rgba(36, 76, 255, 0.75), 0 -2px 0 rgba(216, 15, 15, 0.75);
                }
                99% {
                  text-shadow: 2px 2px 0 rgba(38, 211, 75, 0.952),
                    2px 0 0 rgba(36, 76, 255, 0.75), 0 -2px 0 rgba(216, 15, 15, 0.75);
                }
                100% {
                  text-shadow: -2px 0 0 rgba(38, 211, 75, 0.952),
                    -2px -2px 0 rgba(36, 76, 255, 0.75),
                    -2px -2px 0 rgba(216, 15, 15, 0.75);
                }
            }
        }
    }
    .idxBanner-pic {
        position: absolute;
        top: 10%;
        right: 5%;
        width: 48%;
        animation-name: BannerMoveToBottom;    /* keyframe */
        animation-duration: 4.5s;    /**/
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite; 
        
        @keyframes BannerMoveToBottom {
            0% { 
                transform: translate(0px, 0px);
            }
            50% { 
                transform: translate(-30px, -35px);
            }
            100% { 
                transform: translate(0px, 0px);
            }
        }
    }
    @media screen and (max-width: 576px) {
        height: 650px;
        .banner-wrap {
            align-items: initial;
            padding-top: 150px;
        }
        .banner-title {
            padding-left: 8%;
            width: 100%;
            .banner-text{
                font-size: 2.5rem;
                line-height: 3.5rem;
                p {
                    &:first-child {
                        clip-path: polygon(0 20%, 100% 15%, 100% 100%, 0 90%);
                    }
                    &:last-child {
                        clip-path: polygon(0 2%, 100% 0%, 100% 100%, 0 100%);
                    }
                }
            }
        }
        .idxBanner-pic {
            top: 38%;
            right: 10%;
            width: 375px;
        }
    }
    @media screen and (min-width: 1501px) {
        height: 900px;
        .banner-title {
            padding-left: 6%;
            .banner-text{
                font-size: 4.5rem;
                line-height: 5.5rem;
            }
        }
        .idxBanner-pic {
            width: 47%;
        }
    }
    @media screen and (max-width: 1300px) {
        height: 650px;
        .banner-title {
            width: 60%;
            .banner-text{
                font-size: 3.5rem;
                line-height: 4.5rem;
            }
        }
        .idxBanner-pic {
            width: 55%;
        }
    }
    @media screen and (max-width: 1080px) {
        .banner-wrap {
            align-items: initial;
            padding-top: 15%;
        }
        .banner-title {
            padding-left: 8%;
            width: 50%;
            .banner-text{
                font-size: 3rem;
                line-height: 4rem;
                p {
                    &:first-child {
                        clip-path: polygon(0 20%, 100% 15%, 100% 100%, 0 90%);
                    }
                    &:last-child {
                        clip-path: polygon(0 2%, 100% 0%, 100% 100%, 0 100%);
                    }
                }
            }
        }
        .idxBanner-pic {
            top: 15%;
            right: 5%;
            width: 60%;
        }
    }
    @media screen and (max-width: 820px) {
        height: 650px;
        .banner-wrap {
            padding-top: 150px;
        }
        .banner-title {
            width: 100%;
            .banner-text{
                font-size: 2.5rem;
                line-height: 3.5rem;
            }
        }
        .idxBanner-pic {
            top: 38%;
            right: 10%;
            width: 475px;
        }
    }
    @media screen and (max-width: 576px) {
        .banner-wrap {
            align-items: initial;
            padding-top: 130px;
        }
        .banner-title {
            .banner-text{
                font-size: 2rem;
                line-height: 3rem;
            }
        }
        .idxBanner-pic {
            top: 40%;
            right: 5%;
            width: 400px;
            animation-duration: 3s; 
            @keyframes BannerMoveToBottom {
                0% { 
                    transform: translate(0px, 0px);
                }
                50% { 
                    transform: translate(-10px, -15px);
                }
                100% { 
                    transform: translate(0px, 0px);
                }
            }
        }
    }
}


/*- About -*/
.idxAbout {
    height: auto;
}
.about-wrap {
    position: relative;
    width: 100%;
    height: 750px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    .about-square {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        width: 65%;
        height: 55%;
        border: solid 2px rgba(126,182,237,0.45);
        background: rgba(255,255,255,.6);
        backdrop-filter: blur(2px);
        // position: relative;
        .square-row {
            @include make-row();
            margin: 0;
            height: 100%;
            width: 100%;
            .about-txt {
                @include make-col(6);
                @include make-col-ready();
                text-align: justify;
                height: 100%;
                padding-top: 1rem;
                padding-right: 2rem;
                display: flex;
                flex-direction: column;
                justify-content: center;
                .about-title {
                    font-size: 3rem;
                    font-weight: 600;
                    margin-bottom: 2rem;
                }
                p:nth-child(2) {
                    font-size: 1.3rem;
                }
            }
            .about-pic {
                @include make-col(6);
                @include make-col-ready();
                height: 100%;
                .witch1-pic {
                    transform: translateY(-7rem);
                    position: relative;
                    img {
                        width: 85%;
                    }
                    .book-Deco {
                        position: absolute;
                        top: 20%;
                        right: -12%;
                        width: 50%;
                        animation-name: Moving;    /* keyframe */
                        animation-duration: 2.5s;    /**/
                        animation-timing-function: ease-in-out;
                        animation-iteration-count: infinite; 
                        
                        @keyframes Moving {
                            0% { 
                                transform: translate(0px, 0px);
                            }
                            50% { 
                                transform: translate(0px, -35px);
                            }
                            100% { 
                                transform: translate(0px, 0px);
                            }
                        }
                    }
                }
            }
        }
        
        // &::before {
        //     content: '';
        //     background: url('../images/logo-flat.png') no-repeat;
        //     background-size: 100%;
        //     width: 150px;
        //     height: 35px;
        //     position: absolute;
        //     top: 5%;
        //     right: 5%;
        // }
        
    }
    &::before {
        content: '';
        background: url('../images/about-bg.png') center;
        background-size: cover;
        position: absolute;
        width: 350%;
        height: 310px;
        top: 10%;
        left: -10%;
        z-index: -1;

        animation-name: maruqee1;
        animation-duration:60s;
        animation-timing-function: linear;
        animation-iteration-count:infinite;
        -webkit-marquee-style:scroll;
        position: absolute;
        @keyframes maruqee1 {
            0% { 
                transform: translate(0px, 0px);
            }
            50% { 
                transform: translate(-20%, 0px);
            }
            100% { 
                transform: translate(0px, 0px);
            }
        }
    }
    &::after {
        content: '';
        background: url('../images/about-bg.png') center;
        background-size: cover;
        position: absolute;
        width: 350%;
        height: 310px;
        top: 55%;
        left: -110%;
        z-index: -1;

        animation-name: maruqee2;
        animation-duration:60s;
        animation-timing-function: linear;
        animation-iteration-count:infinite;
        -webkit-marquee-style:scroll;
        position: absolute;
        @keyframes maruqee2 {
            0% { 
                transform: translate(0px, 0px);
            }
            50% { 
                transform: translate(20%, 0px);
            }
            100% { 
                transform: translate(0px, 0px);
            }
        }
    }

    @media screen and (min-width: 1501px) {
        height: 800px;
        .about-square {
            width: 60%;
            height: 60%;
            .square-row {
                .about-txt {
                    font-size: 1.2rem;
                    width: 55%;
                    padding-top: 1rem;
                    padding-right: 2rem;
                    .about-title {
                        font-size: 3.3rem;
                    } 
                    p:nth-child(2) {
                        font-size: 1.5rem;
                    }
                }
                .about-pic {
                    width: 455px;
                    .witch1-pic {
                        .book-Deco {
                            position: absolute;
                            top: 25%;
                            right: -3%;
                        }
                    }
                }
            }
            
            &::before {
                width: 250px;
                height: 60px;
                top: 5%;
                right: 5%;
            }
        }
    }
    @media screen and (max-width: 1300px) {
        height: 650px;
        .about-square {
            width: 65%;
            height: 50%;
            .square-row {
                .about-txt {
                    .about-title {
                        font-size: 2.8rem;
                    }
                    p:nth-child(2) {
                        font-size: 1rem;
                    }
                }
                .about-pic {
                    transform: translateY(2rem);
                    z-index: 1;
                    .witch1-pic {
