    @media screen and (max-width: 1080px) {
        .about-square {
            width: 65%;
            height: 55%;
            .square-row {
                .about-txt {
                    width: 60%;
                }
                .about-pic {
                    z-index: 1;
                    .witch1-pic {
                        width: 110%;
