    @media screen and (max-width: 576px) {
        height: 800px;
        .about-square {
            width: 75%;
            height: 65%;
            padding-top: 2rem;
            .square-row {
                .about-txt {
                    @include make-col(12);
                    @include make-col-ready();
                    height: 40%;
                    order: 1;
                    padding: 2rem;
                    .about-title {
                        font-size: 2rem;
                        text-align: center;
                    }
                    p:nth-child(2) {
