                        text-align: center;
                    }
                }
                .about-pic {
                    @include make-col(12);
                    @include make-col-ready();
                    height: 60%;
                    z-index: 1;
                    order: 2;
                    .witch1-pic {
                        transform: translate(-1rem,-2rem);
                        width: auto;
                        height: 100%;
                        text-align: center;
                        img {
                            width: 250px;
                        }
                        .book-Deco {
                            top: 35%;
                            right: -15%;
                        }
                    }
                }
            }
            
        }
        &::before {
            top: 0%;
        }
        &::after {
            width: 440%;
            top: 65%;
        }
    }
    
}

