/*- idxContact -*/
.idxContact {
    background: url('../images/idx-contact-bg.png')no-repeat top left;
    background-size: cover;
    width: 100%;
    height: 800px;
    margin-top: -30%;
    position: relative;
    overflow: hidden;
    .contact-pic {
        position: absolute;
        top: 30%;
        left: 10%;
        width: 30%;
        animation-name: contact-scale;    /* keyframe */
        animation-duration: 2.5s;    /**/
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite; 
        
        @keyframes contact-scale {
            0% { 
                transform: scale(.9);
            }
            50% { 
                transform: scale(1);
            }
            100% { 
                transform: scale(.9);
            }
        }
        .contact-pic-wrap {
            position: relative;
            .contact-deco1 {
                width: 120%;
                height: 120%;
                position: absolute;
                right: -14%;
                top: 6%;
                z-index: -1;
                .idxContact-deco1 {
                    animation: orbit-1 4s linear infinite;
                    position: absolute;
                    top:0%;
                    left:0;
                    transform:rotate(100deg);
                }
                @keyframes orbit-1{
                    0% {transform:rotate(0deg);}
                    100% {transform: rotate(-360deg); }
                }
            }
            .contact-deco2 {
                width: 100%;
                height: auto;
                position: absolute;
                right: -10%;
                top: 48%;
                animation: Moving 2s ease-in-out infinite;
                .contact-deco2inner {
