/*- Services Page - touch -*/
.servicePage_touch {
    background: url('../images/servicePage-touchBg.png')no-repeat top left;
    background-size: cover;
    width: 100%;
    padding: 1rem 0;
    position: relative;
    overflow: hidden;
    .touch-row {
        @include make-row();
        margin: 0;
        width: 100%;
    }
    .touch-pic {
        @include make-col(5);
        @include make-col-ready();
        width: 25%;
        animation-name: touch-scale;    /* keyframe */
        animation-duration: 2.5s;    /**/
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite; 
        
        @keyframes touch-scale {
            0% { 
                transform: scale(.9);
            }
            50% { 
                transform: scale(1);
            }
            100% { 
                transform: scale(.9);
            }
        }
        .touch-pic-wrap {
            position: relative;
            transform: translate(-10%,10%);
        }
        
    }
    .touch-wrap {
        @include make-col(6);
        @include make-col-ready();
        margin: auto;
        padding-left: 3rem;
        .touch-item {
            padding-left: 13%;
            padding-top: 5%;
            .touch-txt {
                p {
                    font-size: 3rem;
                    font-weight: 800;
                    letter-spacing: 3px;
                }
            }
            
        }
        .touch-cta {
            display: inline-block;
            transform: translateY(10%);
            margin-top: 3rem;
            animation-name: Scaling;    /* keyframe */
            animation-duration: 1s;    /**/
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite; 
            
            @keyframes Scaling {
                0% { 
                    transform: scale(1);
                }
                50% { 
                    transform: scale(1.1);
                    opacity: .6;
                }
                100% { 
                    transform: scale(1);
                }
            }
            .touch-btn {
                padding: .8rem;
                font-size: 1rem;
                background: #0F3E95;
                color: white;
                text-decoration: none;
            }
        }
    }

    @media screen and (min-width: 1501px) {
        .touch-pic {
            .touch-pic-wrap {
                transform: translateY(15%);
            }
        }
        .touch-wrap {
            padding-left: 5%;
            .touch-txt {
                p {
                    font-size: 3.5rem;
                }
            }
        }
        .touch-cta {
            .touch-btn {
                padding: 1rem;
                font-size: 1.5rem;
            }
        }
    }
    @media screen and (max-width: 1300px) {
        .touch-wrap {
            padding-left: 2rem;
            .touch-item {
                padding-left: 8%;
                padding-top: 5%;
                .touch-txt {
                    p {
                        font-size: 2.2rem;
                        font-weight: 800;
                        letter-spacing: 3px;
                    }
                }
                
            }
        }
    }
    @media screen and (max-width: 1080px) {
        padding: 3rem 0;
    }
    @media screen and (max-width: 820px) {
        height: 700px;
        .touch-pic {
            @include make-col(12);
            @include make-col-ready();
            .touch-pic-wrap {
                transform: translate(0,0);
                text-align: center;
                img {
                    width: 80%;
                }
            }
        }
        .touch-wrap {
            @include make-col(12);
            @include make-col-ready();
            padding-top: 3rem;
        }
    }
    @media screen and (max-width: 576px) {
        height: 600px;
        .touch-pic {
            .touch-pic-wrap {
                transform: translate(0,0);
                text-align: center;
                img {
                    width: 100%;
                }
            }
        }
        .touch-wrap {
            @include make-col(12);
