.galleryPage_gallery {
    position: relative;
    overflow: hidden;
    margin-top: -11%;
    background: url('../images/servicePage-capBg.svg') no-repeat top right;
    background-size: cover;
    .galleryPage-galleryWrap {
        padding: 10% 0 5%;
        .gallery-title {
            display: flex;
            justify-content: flex-end;
            padding-right: 4rem;
            h2 {
                font-size: 3.5rem;
                font-weight: 600;
                position: relative;
                &::before {
                    content: '';
                    background: #BAD3EB;
                    background-size: 100%;
                    width: 100%;
                    height: 2px;
                    position: absolute;
                    left: 0%;
                    bottom: -10%;
                }
            }
            
        }

        .gallery-wrap {
            .gallery-row{
                grid-gap: 15px;
                display: flex;
                flex-wrap: wrap;
                display: grid;
                margin: 10% auto 5%;
                grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
                grid-auto-rows: minmax(150px, auto);
                .gallery-item {
                    flex: 1 1 200px;
                    overflow: hidden;
                    border-radius: 5px;
                    .gallery-outside {
                        width: 100%;
                        height: 0;
                        padding-top: 100%;
                        position: relative;
                        overflow: hidden;
                        background: #0F3E95;
                        img {
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            width: auto;
                            height: 100%;
                            -webkit-transform: translate(-50%, -50%) scale(1.01);
                            transform: translate(-50%, -50%) scale(1.01);
                            transition: all 0.3s ease-in-out;
                        }
                    }
                    &:hover {
                        .gallery-outside {
                            img {
                                transform: translate(-50%, -50%) scale(1.2);
                                transition: all 0.3s ease-in-out;
                            }
                        }
                    }
                    &:nth-child(2n+1) {
                        grid-row-end: span 2;
                        .gallery-outside {
                            width: 100%;
                            height: 0;
                            padding-top: calc(200% + 15px);
                            position: relative;
                            overflow: hidden;
                            background: #0F3E95;
                            img {
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                width: auto;
                                height: 100%;
                                -webkit-transform: translate(-50%, -50%) scale(1.01);
                                transform: translate(-50%, -50%) scale(1.01);
                                transition: all 0.3s ease-in-out;
                            }
                        }
                        &:hover {
                            .gallery-outside {
                                img {
                                    transform: translate(-50%, -50%) scale(1.2);
                                    transition: all 0.3s ease-in-out;
                                }
                            }
                        }
                    }
                    &:nth-child(6n+1) {
                        grid-column-end: span 2;
                        grid-row-end: span 2;
                        .gallery-outside {
                            width: 100%;
                            height: 0;
                            padding-top: 100%;
                            position: relative;
                            overflow: hidden;
                            background: #0F3E95;
                            img {
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                width: auto;
                                height: 100%;
                                -webkit-transform: translate(-50%, -50%) scale(1.01);
                                transform: translate(-50%, -50%) scale(1.01);
                                transition: all 0.3s ease-in-out;
                            }
                        }
                        &:hover {
                            .gallery-outside {
                                img {
                                    transform: translate(-50%, -50%) scale(1.2);
                                    transition: all 0.3s ease-in-out;
                                }
                            }
                        }
                    }
                }
                @supports (display: grid) {
                    .wrapper > * {
                      margin: 0;
                    }
                }
            }
        }
        
    }
    @media screen and (max-width: 1300px) {
        .galleryPage-galleryWrap {
            .gallery-title {
                h2 {
                    font-size: 3rem;
                }
            }
                
        }
    }
    @media screen and (max-width: 1080px) {
        .galleryPage-galleryWrap {
            .gallery-wrap {
                .gallery-row{
                    .gallery-item {
                        &:nth-child(7) {
                            grid-column-end: span 1;
                            grid-row-end: span 1;
                        }
                        &:nth-child(8) {
                            grid-column-end: span 2;
                            grid-row-end: span 2;
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-width: 820px) {
        .galleryPage-galleryWrap {
            padding: 100px 0 5%;
        }
    }
    @media screen and (max-width: 576px) {
        margin-top: -180px;
        .galleryPage-galleryWrap {
            .gallery-title {
                h2 {
                    font-size: 2rem;
                    font-weight: 700;
                }
            } 
            .gallery-wrap {
                .gallery-row{
                    .gallery-item {
                        &:nth-child(n) {
                            grid-column-end: span 1;
                            grid-row-end: span 1;
                        }
                    }
                }
            }
        }
    }
}


// pagination //
.pagination-sec {
    display: flex;
    justify-content: center;
    margin: 2rem 0 .5rem;
    .pagination {
        display: flex;
        align-items: center;
    }
    .page-item {
        &.active {
            .page-link {
                background: #35599B !important;
                color: white;
                border-color: #35599B;
            }
        }
        .page-link {
            border: none;
            &:hover {
                background: #35599B;
                color: white;
                border-color: #35599B;
            }
        }
        &:nth-child(1) {
            width: 20%;
            margin-right: 1rem;
            .page-link {
                img {
                    transform: rotate(180deg);
                }
                &:hover {
                    background: transparent;
                }
            }
            
        }
        &:last-child {
            width: 20%;
            margin-left: 1rem;
            .page-link {
                &:hover {
                    background: transparent;
                }
            }
        }
    }
    
}
